<template>
    <div class="box">
         <div class="headbox">
            <el-form :data="formProduct">
                <el-form-item label="" :label-width="lableWidth">
                    <el-input v-model="formProduct.name" style="width:300px"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="lableWidth">
                    <el-cascader :options="options" :props="props" change-on-select @change="handleItemChange"></el-cascader>
                    <span><span style="color:red">{{lei}}</span></span>
                </el-form-item>
                <!-- <el-form-item label="" :label-width="lableWidth">
                <el-input type="textarea" v-model="formProduct.content" style="width:300px"></el-input>
                </el-form-item> -->
            </el-form>
        </div>
        <div class="contentbox">
            <label>SKU</label>
            <el-button type="primary" style="margin-left:35px;" @click="add">SKU</el-button>
            <el-table :data="SKUdata" style="width: 100%">
                <el-table-column prop="sku_name" label=""></el-table-column>
                <el-table-column prop="firm_id" label="">
                    <template slot-scope="scope">
                        <span>{{scope.row.firm_id == 0 ? '' : ''}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="maximum_delivery" label=""></el-table-column>
                <el-table-column prop="slowest_delivery" label=""></el-table-column>
                <el-table-column prop="starting_quantity" label=""></el-table-column>
                <el-table-column prop="unit" label=""></el-table-column>
                <el-table-column prop="logo" label="logo">
                    <template slot-scope="scope">
                        <span>{{scope.row.logo == 0 ? '' : ''}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="cost_price" label=""></el-table-column>
                <el-table-column prop="selling_price" label=""></el-table-column>
                <el-table-column label="" width="155">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="edit(scope.row)"></el-button>
                        <el-button type="danger" size="mini" @click="editSKU(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog title="SKU" :visible.sync="dialogFormProduct">
                <el-form ref="Product" :model="Product" inline>
                    <el-form-item label="" :label-width="lableWidth" prop="category">
                       <el-input v-model="Product.category" style="width:217px"></el-input>
                    </el-form-item> 
                    <el-form-item label="" :label-width="lableWidth" prop="firm_id">
                        <el-radio v-model="Product.firm_id" label="1"></el-radio>
                       <el-radio v-model="Product.firm_id" label="0"></el-radio>
                    </el-form-item>
                    <br>
                    <el-form-item label="" :label-width="lableWidth" prop="fastTime">
                       <el-input v-model="Product.fastTime" style="width:217px"></el-input>
                    </el-form-item> 
                    <el-form-item label="" :label-width="lableWidth" prop="lastTime">
                       <el-input v-model="Product.lastTime" style="width:217px"></el-input>
                    </el-form-item> 
                     <br>
                    <el-form-item label="" :label-width="lableWidth" prop="StartingQuantity">
                       <el-input v-model="Product.StartingQuantity" style="width:100px"></el-input>
                    </el-form-item> 
                    <el-form-item label="" :label-width="lableWidth" prop="unit">
                       <el-input v-model="Product.unit" style="width:100px"></el-input>
                    </el-form-item> 
                    <el-form-item label="logo" :label-width="lableWidth" prop="Customized">
                       <el-radio v-model="Product.Customized" label="1"></el-radio>
                       <el-radio v-model="Product.Customized" label="0"></el-radio>
                    </el-form-item> 
                    <br>
                    <el-form-item label="" :label-width="lableWidth" prop="cost">
                       <el-input v-model="Product.cost" style="width:217px"></el-input>
                    </el-form-item> 
                    
                    <el-form-item label="" :label-width="lableWidth" prop="Sell">
                       <el-input v-model="Product.Sell" style="width:217px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="" :label-width="lableWidth" prop="cost">
                       <el-input v-model="Product.boxing" style="width:217px"></el-input>
                    </el-form-item> 
                    
                    <el-form-item label="" :label-width="lableWidth" prop="Sell">
                       <el-input v-model="Product.size" style="width:217px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="" :label-width="lableWidth" prop="cost">
                       <el-input v-model="Product.specifications" style="width:217px"></el-input>
                    </el-form-item> 
                    
                    <el-form-item label="" :label-width="lableWidth" prop="Sell">
                       <el-input v-model="Product.material_quality" style="width:217px"></el-input>
                    </el-form-item>
                    <br>



                    <el-form-item label="" :label-width="lableWidth">
                      <el-upload class="avatar-uploader" 
                        :multiple="true"
                        accept="image/jpeg,image/gif,image/png,image/bmp"
                        :show-file-list="false" 
                        action="/File/product_sku_img/"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="Product.imageUrl" :src="Product.imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>   
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormProduct = false"> </el-button>
                    <el-button type="primary" @click="addSKU"> </el-button>
                </div>
            </el-dialog>
        </div>

       <div class="contentfoot">
            <label></label>
            <el-button type="primary" style="margin-left:35px;" @click="addSupplierPrice"></el-button>
            <el-table :data="SupplierPrice">
                <el-table-column label="" prop="number"></el-table-column>
                <el-table-column label="" prop="cost_price"></el-table-column>
                <el-table-column label="" prop="selling_price"></el-table-column>
                <el-table-column label="" prop="supplier_name"></el-table-column>
                <el-table-column label="" prop="remarks"></el-table-column>
                <el-table-column label="" width="155">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="SupplierPriceModify(scope.row)"></el-button>
                        <el-button type="danger" size="mini" @click="SupplierPriceEdit(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>    
            <el-dialog title="" :visible.sync="dialogFormSupplierPrice">
                <el-form ref="SteppedPrice" v-model="SteppedPrice">
                    <el-form-item label="" prop="supplier" :label-width="lableWidth">
                        <el-select v-model="SteppedPrice.supplier_id" filterable placeholder="" style="width:217px">
                            <el-option v-for="item in optionSupplier" :key="item.id" :label="item.name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="product" :label-width="lableWidth">
                       <el-select v-model="SteppedPrice.product" filterable placeholder="" style="width:217px">
                            <el-option v-for="item in optionproduct" :key="item.id" :label="item.sku_name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="number" :label-width="lableWidth">
                        <el-input v-model="SteppedPrice.number" style="width:217px"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="costPrice" :label-width="lableWidth">
                        <el-input v-model="SteppedPrice.cost_price" style="width:217px"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sellPrice" :label-width="lableWidth">
                        <el-input v-model="SteppedPrice.selling_price" style="width:217px"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="remark" :label-width="lableWidth">
                        <el-input v-model="SteppedPrice.remarks" type="textarea" style="width:217px"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormSupplierPrice = false"> </el-button>
                    <el-button type="primary" @click="addingSteppedPrice"> </el-button>
                </div>
            </el-dialog>
        </div>   

        <label></label> 
        <vue-ueditor-wrap v-model="msg" :config="myConfig"  style="margin-top:10px;padding-bottom:20px"></vue-ueditor-wrap>

        <el-button type="primary" @click="comfirm"></el-button>
    </div>
</template>
<script>
import VueUeditorWrap from 'vue-ueditor-wrap'
import {mapActions} from 'vuex';
export default {
    data(){
        return{
            msg:'',
            formProduct:{
                name:'',
                category:'',
                content:''
            },
            options:[
                {
                    id:1,
                    value:''
                },
                {
                    id:2,
                    value:''
                },
                {
                    id:3,
                    value:''
                }
            ],
            props:{
                value:'id',
                label:'name',
                children:'children'
            },
            lableWidth:'100px',
            SKUdata:[
            ],
            lei:'',
            class_id:'',
            dialogFormProduct:false,
            Product:{
                category:'',
                specialSection:'',
                firm_id:'',
                fastTime:'',
                lastTime:'',
                StartingQuantity:'',
                unit:'',
                Customized:'',
                cost:'',
                Sell:'',
                product_id:'',
                id:'',
                imageUrl:'',
                boxing:'',
                size:'',
                specifications:'',
                material_quality:'',
            },
            optionSupplier:[
                {
                    label:'',
                    value:1,
                },
                {
                    label:'',
                    value:2,
                },
                {
                    label:'',
                    value:3,
                },
                {
                    label:'',
                    value:4,
                }
            ],  
            optionproduct:[],
            SupplierPrice:[
            ],
            dialogFormSupplierPrice:false,
            SteppedPrice:{
                product_id:'',
                supplier_id:'',
                product:'',
                number:'',
                cost_price:'',
                selling_price:'',
                remarks:'',
                id:'',
            },
            //baidu fuwenben
            myConfig: {
                // 
                autoHeightEnabled: false,
                // 
                initialFrameHeight: 240,
                // 
                initialFrameWidth: '100%',
                // 
                //serverUrl: 'http://35.201.165.105:8000/controller.php',
                // UEditor  vue-cli vue-ueditor-wrap 2
                UEDITOR_HOME_URL: '/static/UEditor/'
            },
        }       
    },
    components: {
        VueUeditorWrap
    },
    methods:{
         ...mapActions("Tabs", ['triggerDeleteTabs']),
        handleItemChange:function(value){
            let a = value.length;
            let b = value[a-1];
            this.formProduct.category = b;
        },
        getEditData(){
             this.axios.get('/erp.Product/product_update?id='+this.$route.params.id).then(res => {

                let shuju = res.data;
                this.formProduct.category = shuju.class_id;
                this.formProduct.name = shuju.name;
                this.lei = shuju.class_name;
                this.class_id = shuju.class_id;
                this.Product.product_id = shuju.id;
                this.SKUdata = shuju.sku;
                this.optionproduct = shuju.sku;
                this.msg = shuju.content;
             }) 
        },
        getData(){
            this.axios.get('/erp.Product/product_class_select').then(res => {
             this.options = res.data.product_class;
            })
        },
        // SKU
        add(){
            this.queryimg();
            this.dialogFormProduct = true;
            this.Product.firm_id = '';
            this.Product.product_id = '';
            this.Product.category = '';
            this.Product.fastTime = '';
            this.Product.lastTime = '';
            this.Product.StartingQuantity = '';
            this.Product.unit = '';
            this.Product.Customized = '';
            this.Product.cost =  '';
            this.Product.Sell = '';
            this.Product.boxing = '';
            this.Product.size = '';
            this.Product.specifications = '';
            this.Product.material_quality = '';
        },
        // 
        queryimg(){
            this.axios.post('/File/file_select',{
                class:'product_file',
                type:'img',
                sku_id:'',
            }).then(res => {
                if(res.data != null){
                    this.Product.img = res.data.src;
                }
            })
        },

        //  SKU
        addSKU(){
            this.axios.post('/erp.Product/product_sku_update',{
                product_id:this.$route.params.id,
                id:this.Product.id,
                firm_id:this.Product.firm_id,
                sku_name:this.Product.category,
                maximum_delivery:this.Product.fastTime,
                slowest_delivery:this.Product.lastTime,
                starting_quantity:this.Product.StartingQuantity,
                unit:this.Product.unit,
                logo:this.Product.Customized,
                cost_price:this.Product.cost,
                selling_price:this.Product.Sell,  
                boxing:this.Product.boxing,
                size:this.Product.size,
                specifications:this.Product.specifications,
                material_quality:this.Product.material_quality,
                img:this.Product.imageUrl,
                }).then(res => {
                    if(res.data.code == 2000){
                    this.$refs['Product'].resetFields(); 
                    this.open(res.data.msg,'success');
                    this.getEditData();
                    this.dialogFormProduct = false;
                    }else{
                    this.open(res.data.msg,'error'); 
                    }   
                })
        },
         // 
        handleAvatarSuccess(res) {
            this.Product.imageUrl = res.src;
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG) {
            this.$message.error('JPG/PNG!');
            }
            if (!isLt2M) {
            this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
        },
        // SKU
        edit(a){
            let a_f;
            let a_l;
            if(a.firm_id == 1){
                a_f = '1';
            }else{
                a_f = '0';
            };
             if(a.logo == 1){
                a_l = '1';
            }else{
                a_l = '0';
            };
            this.dialogFormProduct = true;
            this.Product.firm_id = a_f;
            this.Product.category = a.sku_name;
            this.Product.fastTime = a.maximum_delivery;
            this.Product.lastTime = a.slowest_delivery;
            this.Product.StartingQuantity = a.starting_quantity;
            this.Product.unit = a.unit;
            this.Product.Customized = a_l;
            this.Product.cost =  a.cost_price.split('.')[0];
            this.Product.Sell = a.selling_price.split('.')[0];
            this.Product.id = a.id;
            this.Product.imageUrl = a.src;
            this.Product.boxing = a.boxing;
            this.Product.size = a.size;
            this.Product.specifications = a.specifications;
            this.Product.material_quality = a.material_quality;
        },
        // SKU
        editSKU(a){
            this.axios.post('/erp.Product/product_sku_delete',{
                id:a.id
            }).then(res => {
                if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.getEditData();
                }else{
                    this.open(res.data.msg,'error'); 
                } 
            })
        },
        
        //  
        addSupplierPrice(){
            this.dialogFormSupplierPrice = true;
            this.SteppedPrice.product_id = this.Product.product_id;
            this.SteppedPrice.supplier_id = '';
            this.SteppedPrice.number = '';
            this.SteppedPrice.cost_price = '';
            this.SteppedPrice.selling_price = '';
            this.SteppedPrice.remarks = '';
            this.SteppedPrice.id = '';
        },
        //  
        SupplierPriceModify(a){
           this.dialogFormSupplierPrice = true;
            this.SteppedPrice.id = a.id;
            this.SteppedPrice.supplier_id = a.supplier_id;
            this.SteppedPrice.product_id = this.Product.product_id;
            this.SteppedPrice.number =a.number;
            this.SteppedPrice.cost_price =a.cost_price;
            this.SteppedPrice.selling_price = a.selling_price;
            this.SteppedPrice.remarks = a.remarks;
        },
        SupplierPriceEdit(a){
           this.axios.post('/erp.Product/product_supplier_delete',{
               id:a.id
           }).then(res => {
               if(res.data.code == 2000){
                   this.open(res.data.msg,'success');
                   this.getSupplierList(this.Product.product_id);
               }else{
                   this.open(res.data.msg,'error');
               } 
           })
        },
        // 
        addingSteppedPrice(){
            //
            if(this.SteppedPrice.id == ''){
                this.axios.post('/erp.Product/product_supplier_add',{
                product_id:  this.SteppedPrice.product_id,
                supplier_id : this.SteppedPrice.supplier_id,
                product_sku_id:this.SteppedPrice.product,
                number : this.SteppedPrice.number,
                cost_price : this.SteppedPrice.cost_price,
                selling_price : this.SteppedPrice.selling_price,
                remarks : this.SteppedPrice.remarks,
                }).then(res => {
                    if(res.data.code == 2000){
                        this.open(res.data.msg,'success');
                        this.getSupplierList(this.SteppedPrice.product_id);
                        this.dialogFormSupplierPrice = false;
                    }else{
                        this.open(res.data.msg,'error');
                    } 
                })
            }else{
                // 
                this.axios.post('/erp.Product/product_supplier_update',{
                id:  this.SteppedPrice.id,
                supplier_id : this.SteppedPrice.supplier_id,
                product_sku_id:this.SteppedPrice.product,
                number : this.SteppedPrice.number,
                cost_price : this.SteppedPrice.cost_price,
                selling_price : this.SteppedPrice.selling_price,
                remarks : this.SteppedPrice.remarks,
                }).then(res => {
                    if(res.data.code == 2000){
                        this.open(res.data.msg,'success');
                        this.getSupplierList(this.SteppedPrice.product_id);
                        this.dialogFormSupplierPrice = false;
                    }else{
                        this.open(res.data.msg,'error');
                    } 
                })
            }
        },
        // 
        getSupplierList(a){
          this.axios.get('/erp.Product/product_supplier_list?id='+a).then(res => {
                this.SupplierPrice = res.data.product_supplier;
                 // console.log(res)
            })  
        },
        // 
        getSupplier(){
            this.axios.get('/erp.supplier/supplier_select').then(res => {
                this.optionSupplier = res.data.supplier;
            })
        },
        comfirm(){
            this.axios.post('/erp.Product/product_update',{
                id:this.$route.params.id,
                content:this.msg,
                name:this.formProduct.name,
                class_id:this.class_id,
            }).then(res => {
                 if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.triggerDeleteTabs(this.$route.path);
                    this.$router.push({ path: "/erp/Product/product_list" });
                }else{
                    this.open(res.data.msg,'error');
                } 
            })
        },

        open(a,b){
            this.$message({
                message: a,
                type: b
            });
        },   
    },
    created(){
      this.getEditData();
      this.getData();
      this.getSupplierList(this.$route.params.id);
      this.getSupplier();
    },
    mounted(){
      console.log(this.$route.params); 
    },
    watch:{
        $route(to){
            if(!this.$route.params.id){

            }else{
                this.getEditData(); // ajax
                this.getSupplierList(this.$route.params.id);
            }
            
        }
    }
}
</script>
<style lang="less" scoped>
.box{
    min-width: 950px;
    margin-top: 10px;
    .contentfoot{
        margin-top: 30px;
        margin-bottom: 30px;
    }
}
.avatar-uploader,.el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader,.el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 160px;
    height: 160px;
    line-height: 160px;
    text-align: center;
  }
  .avatar {
    width: 170px;
    height: 170px;
    display: block;
  }
</style>
