<template>
    <div class="box">
        <div class="box_head">
            <label></label>
            <el-input v-model="seach" style="width:217px"></el-input>
            <el-button type="primary" style="margin-left:40px;"></el-button>
        </div>
        <div class="content_box">
            <el-table :data="tableData">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span>{{ scope.row.name}}{{ scope.row.class_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="number"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 0"></span>
                        <span v-if="scope.row.status == 3"></span>
                        <span v-if="scope.row.status == 5"></span>
                        <span v-if="scope.row.status == 6"></span>
                        <span v-if="scope.row.status == 9"></span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="user_name"></el-table-column>
                <el-table-column label="" prop="add_time"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary"  v-if="scope.row.status == 0" size="small" @click="verify(scope.row)"></el-button>
                        <el-button type="danger"  v-if="scope.row.status == 0" size="small" @click="deteles(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="block" style="margin-top: 10px;">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="per_page"
                layout="total, prev, pager, next"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            seach:'', // 
            tableData:[], // table 
             //
            currentPage:'',//
            total:'',//
            per_page:'',//
            last_page:''//
        }
    },
    methods:{
        // 
        gettableData(){
            this.axios.get('/erp.Product/product_product_stock_lists').then(res => {
                this.tableData = res.data.product_stock.data;
                this.currentPage = res.data.product_stock.current_page;
                this.total = res.data.product_stock.total;
                this.per_page = res.data.product_stock.per_page;
                this.last_page = res.data.product_stock.last_page;
            })
        },
        // 
        handleCurrentChange(val) {
            const loading = this.$loading({
                lock: true,
                text: '',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/erp.Product/product_product_stock_lists',{
                page:val,
            }).then(res => {
                this.tableData = res.data.product_stock.data;
                this.currentPage = res.data.product_stock.current_page;
                loading.close();
            
            })  
        },
        // 
        deteles(a){
            this.axios.post('/erp.Product/product_product_stock_delete',{
                id:a.id
            }).then(res => {
                if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.gettableData();
                }else{
                    this.open(res.data.msg,'error');
                } 
            })
        },
        // 
        verify(a){
           this.axios.post('/erp.Product/product_product_stock_status',{
                id:a.id
            }).then(res => {
                if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.gettableData();
                }else{
                    this.open(res.data.msg,'error');
                } 
            })  
        },
         open(a,b){
            this.$message({
                message: a,
                type: b
            });
        }, 
    },
    created(){
        this.gettableData();
    },
    watch:{
      $route(to){
        
          this.gettableData();
        }
    }
}
</script>

<style lang="less" scoped>
.box{
    .box_head{
        margin-top: 20px;
    }
}
</style>
