<template>
    <div class="box">
        <div class="head_box">
            <label></label>
            <el-input v-model="seach" style="width:217px"></el-input>
            <el-button style="margin-left:20px"></el-button>
        </div>
        <el-table :data="tableData" style="margin-top:20px">
            <el-table-column label="" prop="customer_count"></el-table-column>
            <el-table-column label="" prop="product_sku_count"></el-table-column>
            <el-table-column label="" prop="add_time" width="160"></el-table-column>
            <el-table-column label="" prop="remarks"></el-table-column>
            <el-table-column label="" prop="user_name"></el-table-column>
            <el-table-column label="" prop="status">
                <template slot-scope="scope">
                    <span v-if="scope.row.status == 0"></span>
                    <span v-if="scope.row.status == 1"></span>
                    <span v-if="scope.row.status == 10"></span>
                    <span v-if="scope.row.status == 20"></span>
                    <span v-if="scope.row.status == 30"></span>
                    <span v-if="scope.row.status == 40"></span>
                    <span v-if="scope.row.status == 50"></span>
                    <span v-if="scope.row.status == 90"></span>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="updata(scope.row)"></el-button>
                    <!-- <el-button type="danger" size="mini" @click="deletes(scope.row)"></el-button> -->
                </template>
            </el-table-column>
        </el-table>
        <div class="block" style="margin-top: 10px;">
            <el-pagination
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="per_page"
            layout="total, prev, pager, next"
            :total="total">
            </el-pagination>
        </div>  
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    data(){
        return{
            seach:'',//
            tableData:[],// 
            currentPage:0,//
            total:0,//
            per_page:0,//
            last_page:0,//    
        }
    },

    methods:{
        // vuex tab
        ...mapActions("customerList", ["triggerReplaceId"]),
        ...mapActions("Tabs", ["triggerAddTabs", "triggerSetActiveIndex"]),

        gettableData(){
            this.axios.get('/Finance/finance_customer_sample_lists').then(res => {
                this.tableData = res.data.sample.data;
                this.currentPage = res.data.sample.current_page;
                this.total = res.data.sample.total;
                this.per_page = res.data.sample.per_page;
                this.last_page = res.data.sample.last_page;
            })
        },
        //  
        handleCurrentChange(val) {
            const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/Finance/finance_customer_sample_lists',{
                page:val,
                company_id:this.value,
                user_name:this.value1,
                customer_name:this.value2,
            }).then(res => {
                this.tableData = res.data.sample.data;
                this.currentPage = res.data.sample.current_page;
                this.total = res.data.sample.total;
                this.per_page = res.data.sample.per_page;
                this.last_page = res.data.sample.last_page;
                loading.close();
            })  
        },
        // 
        updata(a){
            this.triggerReplaceId(a.id);
            this.$router.push({ path: `/Finance/finance_customer_sample_lists_details/${a.id}`,params:{
                    userId:a.id,
                    name:`/Finance/finance_customer_sample_lists_details/${a.id}`,
                }
            });
            let ins1 = {
                route: `/Finance/finance_customer_sample_lists_details/${a.id}`,
                name: "",
            };
            let ins2 = `/Finance/finance_customer_sample_lists_details/${a.id}`;
            this.triggerAddTabs(ins1);
            this.triggerSetActiveIndex(ins2);
        },
        //
        deletes(a){
            // this.axios.post('/crm.Sample/sample_delete',{
            //     id:a.id
            // }).then(res => {
            //     if(res.data.code == 2000){
            //         this.gettableData();
            //         this.open(res.data.msg,'success')
            //     }else{
            //         this.open(res.data.msg,'error')
            //     }
            // })
            alert(1);
        },
        // 
        open(a,b){
            this.$message({
                message: a,
                type: b
            });
        },
    },
    created(){
        this.gettableData();
    }
}
</script>

