<!--  -->
<template>
  <div class="box">
    <div class="box_head">
      <label></label>
      <el-input v-model="value" placeholder="" style="width:217px"></el-input>
      <el-input v-model="value1" placeholder="" style="width:217px;margin-left:20px;"></el-input>
      <el-date-picker v-model="value2" type="daterange" format="yyyy-MM-dd" value-format="yyyy-MM-dd" range-separator="" start-placeholder="" end-placeholder="" style="margin-left:20px;"></el-date-picker>
      <el-button @click="seach" style="margin-left:20px;"></el-button>
    </div>

    <div class="box_content">
      <el-table
        :data="tableData"
        style="width: 100%;"
      >
        <el-table-column
          prop="id"
          label="" 
        >
        </el-table-column>
        <el-table-column
          prop="user_name"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customer_name"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="sales_time"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="total_price"
          label=""
        >
        </el-table-column>

        <el-table-column
          prop="invoice_type"
          label=""
        >
          <template slot-scope="scope">
            <span>{{ Number(scope.row.total_price) - Number(scope.row.total_cost_price)- Number(scope.row.user_price) - Number(scope.row.logistics_price) | keepTowNum}}</span>
          </template>
        </el-table-column>

        <el-table-column
          prop="status"
          label=""
        >
          <template slot-scope="scope">
              <span v-if="scope.row.status == 10"></span>
              <span v-if="scope.row.status == 1"> </span>
              <el-tooltip v-if="scope.row.status == 9" class="item" effect="dark" :content="scope.row.status_txt" placement="top-start">
                <span style="color:#409EFF"></span>
              </el-tooltip>
          </template>
        </el-table-column>

        <el-table-column
          prop="status"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="status"
          label=""
        >
        </el-table-column>

        <el-table-column  label="">
          <template  slot-scope="scope">
            <el-button @click="examine(scope.row)" type="primary" size="small">{{scope.row.status == 1?'':''}}</el-button>
          </template>  
        </el-table-column>
      </el-table>

      <div class="block" style="margin-top: 10px;">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="per_page"
          layout="total, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
import { mapActions } from "vuex";
import { watch } from 'fs';

export default {
  filters:{
      keepTowNum:function(value){
        value = Number(value);
        return value.toFixed(2)
      }
    },
  data() {
    return {
      value:'',
      value1:'',
      value2:['',''],

      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      options1: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      options2: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      tableData:[], // 
      currentPage:0,//
      total:0,//
      per_page:0,//
      last_page:0//
    };
  },
  methods: {
    // vuex tab
    ...mapActions("customerList", ["triggerReplaceId"]),
    ...mapActions("Tabs", ["triggerAddTabs", "triggerSetActiveIndex"]),
    // 
    examine(a){
      //alert(a.id);
      console.log(a.status);
      this.triggerReplaceId(a.id);
      this.$router.push({ path: `/Finance/auditDetails/${a.id}`,params:{userId:a.id,name:`/Finance/auditDetails/${a.id}`}});
      let ins1 = {
        route: `/Finance/auditDetails/${a.id}`,
        name: "",
        id: a.id
      };
      let ins2 = `/Finance/auditDetails/${a.id}`;
      this.triggerAddTabs(ins1);
      this.triggerSetActiveIndex(ins2);
    },
  
  
    getOrderList(){
      this.axios.get('/Finance/finance_order_audit_list').then(res => {
        this.tableData = res.data.order.data;
        this.currentPage = res.data.order.current_page;
        this.total = res.data.order.total;
        this.per_page = res.data.order.per_page;
        this.last_page = res.data.order.last_page;
      })
    },
    handleCurrentChange(val) {
        const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.axios.post('/Finance/finance_order_audit_list',{
            page:val,
            username:this.value,
            name:this.value1,
            start_time: this.value2[0],
            end_time: this.value2[1]
          }).then(res => {
          this.tableData = res.data.order.data;
          this.currentPage = res.data.order.current_page;
          this.total = res.data.order.total;
          this.per_page = res.data.order.per_page;
          this.last_page = res.data.order.last_page;
          loading.close();
          //console.log(this.customerListInfo)
        })  
      },
      seach(){
        const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.axios.post('/Finance/finance_order_audit_list',{
            username:this.value,
            name:this.value1,
            start_time: this.value2[0],
            end_time: this.value2[1]
          }).then(res => {
          this.tableData = res.data.order.data;
          this.currentPage = res.data.order.current_page;
          this.total = res.data.order.total;
          this.per_page = res.data.order.per_page;
          this.last_page = res.data.order.last_page;
          loading.close();
          //console.log(this.customerListInfo)
        })
      },
    // 
    open(a,b){
      this.$message({
        message: a,
        type: b
      });
    },
  },
  created(){
     this.getOrderList();
  },
  watch:{
    $route(to){
      this.getOrderList();
    }
  }
};
</script>
<style lang="less" scoped>
.box {
  min-width: 890px;
  .box_head {
    margin-top: 20px;
  }
}
</style>
