<template>
    <div class="box">
        <div class="head_box">
            <label></label>
            <el-input placeholder="" v-model="seach" style="width:217px;margin-left:10px"></el-input>
            <el-button type="primary" @click="seachName"  style="margin-left:20px;"></el-button>
        </div>
        <div class="content_box">
                <el-table :data="tableData">
                <el-table-column label="" prop="type"></el-table-column>
                <el-table-column label="" prop="rise"></el-table-column>
                <el-table-column label="" prop="tax_number"></el-table-column>
                <el-table-column label="" prop="money"></el-table-column>
                <el-table-column label="" prop="category"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 0">
                            
                        </span>
                        <span v-if="scope.row.status == 3">
                            
                        </span>
                        <span v-if="scope.row.status == 5">
                            
                        </span>
                        <span v-if="scope.row.status >= 9">
                            
                        </span>
                    </template>
                </el-table-column>
                <el-table-column label="">
                        <template slot-scope="scope">
                            <el-button type="primary" v-if="scope.row.status < 9" size="mini" @click="Audit(scope.row)"></el-button>
                            <br>
                            <el-button type="primary" size="mini" style="margin-top:5px;" @click="express(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>

                 <el-dialog title="" :visible.sync="dialogSend" width="60%">
                     <el-table :data="SendData">
                            <el-table-column label="" prop="express_name"></el-table-column>
                            <el-table-column label="" prop="numbers"></el-table-column>
                            <el-table-column label="" prop="money"></el-table-column>
                            <el-table-column label="" prop="add_time"></el-table-column>
                            <el-table-column label="">
                                <template slot-scope="scope">
                                    <el-button type="primary" @click="examine(scope.row)" size="small"> </el-button>
                                </template>
                            </el-table-column>
                     </el-table>
                 </el-dialog>
        </div>
        <div class="block" style="margin-top: 10px;">
                <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="per_page"
                layout="total, prev, pager, next"
                :total="total">
                </el-pagination>
            </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            seach:'',
            tableData:[], // 
            currentPage:0,//
            total:0,//
            per_page:0,//
            last_page:0,//
            dialogSend:false, //
            SendData:[],
            id:'',
        }
    },
    methods:{
        // 
        seachName(){
            this.axios.post('/Finance/finance_invoice_list',{
                rise:this.seach,
            }).then(res => {
                this.tabledata = res.data.invoice.data;
                this.currentPage = res.data.invoice.current_page;
                this.total = res.data.invoice.total;
                this.per_page = res.data.invoice.per_page;
                this.last_page = res.data.invoice.last_page;
            })
        },
        gettableData(){
            this.axios.get('/Finance/finance_invoice_list').then(res => {
                this.tableData = res.data.invoice.data;
                this.currentPage = res.data.invoice.current_page;
                this.total = res.data.invoice.total;
                this.per_page = res.data.invoice.per_page;
                this.last_page = res.data.invoice.last_page;
            })
        },
        // 
        handleCurrentChange(val) {
            const loading = this.$loading({
                lock: true,
                text: '',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/Finance/finance_invoice_list',{
                page:val,
            }).then(res => {
                this.tableData = res.data.invoice.data;
                this.currentPage = res.data.invoice.current_page;
                loading.close();
            })  
        },
        // 3
        Audit(a){
            this.axios.post('/Finance/finance_invoice_status',{
                id:a.id
            }).then(res => {
                 if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.gettableData();
                }else{
                    this.open(res.data.msg,'error');
                }
            })
        },
        // 
        express(a){
            
            this.id = a.id; 
            this.axios.get('/Finance/finance_invoice_express_select?id='+a.id).then(res => {
                this.SendData = res.data;
             })
             this.dialogSend = true;
          // alert(a.id)   
        },
        // 
        examine(a){
             this.axios.post('/Finance/finance_invoice_express_status',{
                 id:a.id
             }).then(res => {
                if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.dialogSend = false;
                    this.id = '';
                }else{
                    this.open(res.data.msg,'error');
                }   
             })
            
        },
        open(a,b){
            this.$message({
                message: a,
                type: b
            });
        },
    },
    created(){
        this.gettableData();
    },
    watch:{
        $route(to){
          this.gettableData();
        }
    }
}
</script>

