<template>
    <div class="box">
        <div class="box_head">
            <label></label>
            <el-select v-model="value" filterable clearable placeholder="">
                <el-option label="" value="">
                </el-option>
                <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
            </el-select>
            <el-input v-model="value1" clearable style="width:217px;margin-left: 20px" placeholder=""></el-input>
            <el-input v-model="value2" clearable style="width:217px;margin-left: 20px" placeholder=""></el-input>
            <el-button style="margin-left: 20px" type="primary" @click="seach"></el-button>
        </div>
        <div class="box_content">
            <el-table :data="tableData">
                <el-table-column label="" width="150px">
                    <template slot-scope="scope">
                        <span>{{scope.row.dep_title}}  {{scope.row.user_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="customer_name"></el-table-column>
                <el-table-column label="" prop="address_name"></el-table-column>
                <el-table-column label="" prop="supplier_name"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span>{{ scope.row.payment == 1 ? '' : ''}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span>{{ state[scope.row.delivery] }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="delivery_time"></el-table-column>
                <el-table-column label="" prop="freight_price"></el-table-column>
                <el-table-column label="" prop="">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 0" style="color:red"></span>
                        <span v-if="scope.row.status == 3" style="color:blue"></span>
                        <span v-if="scope.row.status == 6"></span>
                        <span v-if="scope.row.status == 9"></span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="145">
                    <template slot-scope="scope">
                        <el-button type="success" size="small" @click="Track(scope.row)"></el-button>
                        
                        <el-button type="primary" size="small" @click="deliver(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div> 
        <div class="block" style="margin-top: 10px;">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="per_page"
          layout="total, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>  
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    data(){
        return{
            // 
            value:'',
            value1:'',
            value2:'',
            options: [], 
            // 
            tableData:[],
            currentPage:0,//
            total:0,//
            per_page:0,//
            last_page:0,//
            state:['','','',''], 
        }
    },
    methods:{
        // vuex tab
        ...mapActions("customerList", ["triggerReplaceId"]),
        ...mapActions("Tabs", ["triggerAddTabs", "triggerSetActiveIndex"]),
        // table
        gettableData(){
            this.axios.get('/erp.Delivery/delivery_order_list').then(res => {
                this.options = res.data.company;
                this.tableData = res.data.purchase_product.data;
                this.currentPage = res.data.purchase_product.current_page;
                this.total = res.data.purchase_product.total;
                this.per_page = res.data.purchase_product.per_page;
                this.last_page = res.data.purchase_product.last_page;
            })
        },
        //  
        handleCurrentChange(val) {
            const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/erp.Delivery/delivery_order_list',{
                page:val,
                company_id:this.value,
                user_name:this.value1,
                customer_name:this.value2,
            }).then(res => {
                this.tableData = res.data.purchase_product.data;
                this.currentPage = res.data.purchase_product.current_page;
                this.total = res.data.purchase_product.total;
                this.per_page = res.data.purchase_product.per_page;
                this.last_page = res.data.purchase_product.last_page;
                loading.close();
            
            })  
        },
        //
        seach(){
            const loading = this.$loading({
                lock: true,
                text: '',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/erp.Delivery/delivery_order_list',{
                company_id:this.value,
                user_name:this.value1,
                customer_name:this.value2,
            }).then(res => {
                this.tableData = res.data.purchase_product.data;
                this.currentPage = res.data.purchase_product.current_page;
                this.total = res.data.purchase_product.total;
                this.per_page = res.data.purchase_product.per_page;
                this.last_page = res.data.purchase_product.last_page;
                loading.close();
            })  
        },
        // 
        Track(a){
            this.triggerReplaceId(a.id);
            this.$router.push({ path: `/erp/Delivery/Track/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,params:{
                    name:`/erp/Delivery/Track/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,
                    supplier_id:a.supplier_id,
                    customer_order_id:a.customer_order_id,
                    customer_order_purchase_id:a.customer_order_purchase_id,
                } 
            });
            let ins1 = {
                route: `/erp/Delivery/Track/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,
                name: "",
                id: a.id
            };
            let ins2 = `/erp/Delivery/Track/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`;
            this.triggerAddTabs(ins1);
            this.triggerSetActiveIndex(ins2);
        },
        // 
        deliver(a){
            this.triggerReplaceId(a.id);
            this.$router.push({ path: `/erp/Delivery/deliver/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,params:{
                    userId:a.id,
                    name:`/erp/Delivery/deliver/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,
                    supplier_id:a.supplier_id,
                    customer_order_id:a.customer_order_id,
                    customer_order_purchase_id:a.customer_order_purchase_id,
                }
            });
            let ins1 = {
                route: `/erp/Delivery/deliver/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`,
                name: "",
            };
            let ins2 = `/erp/Delivery/deliver/${a.supplier_id}/${a.customer_order_id}/${a.customer_order_purchase_id}`;
            this.triggerAddTabs(ins1);
            this.triggerSetActiveIndex(ins2);
        },



        // 
        open(a,b){
            this.$message({
                message: a,
                type: b
            });
        },
    },
    created(){
        this.gettableData();
    },
    watch:{
        $route(to){
           this.gettableData();
        }
    }
}
</script>

<style lang="less" scoped>
.box {
  min-width: 890px;
  .box_head {
    margin-top: 20px;
  }
}
</style>

