<!--  -->
<template>
  <div class="box">
    <div class="box_head">
      <label></label>
      <el-select
        v-model="value"
        placeholder=""
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="value1"
        collapse-tags
        style="margin-left: 20px;"
        placeholder=""
      >
        <el-option
          v-for="item in options1"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="value2"
        collapse-tags
        style="margin-left: 20px;"
        placeholder=""
      >
        <el-option
          v-for="item in options2"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>

    <div class="box_content">
      <el-table
        :data="tableData"
        height="318"
      >
        <el-table-column
          prop="companyName"
          label=""
          fixed
        >
        </el-table-column>
        <el-table-column
          prop="abbreviation"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="province"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="city"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="address"
          label="/"
        >
        </el-table-column>
        <el-table-column
          prop="customer"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="order"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="contact"
          label=""
        >
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
          <el-button
            @click.native="deleteRow(scope.row)"
            type="text"
            size="small"
          >
            
          </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-table
        :data="customerInfo"
        style="width: 100%;margin-top:40px;text-align: center;"
        border
      >
        <el-table-column
          prop="customerName"
          label=""
          fixed
        >
        </el-table-column>
        <el-table-column
          prop="customerSex"
          label=""
          width="50"
        >
        </el-table-column>
        <el-table-column
          prop="customerBumen"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerZhiwei"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerAge"
          label=""
          width="50"
        >
        </el-table-column>
        <el-table-column
          prop="customerBrith"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerPhone"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerZphone"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerJibie"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="customerXiadan"
          label=""
        >
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
          <el-button
            @click.native="update(scope.row)"
            type="text"
            size="small"
          >
            
          </el-button>
          </template>
        </el-table-column>

      </el-table>
    </div>

    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      width="70%"
    >
      <el-form :model="form" :inline="true">
        <el-form-item
          label=""
          :label-width="formLabelWidth"
         
        >
          <el-input
            style="width:180px"
            v-model="form.customerName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-select
            v-model="form.customerSex"
            placeholder=""
            style="width:180px"
          >
            <el-option
              label=""
              value=""
            ></el-option>
            <el-option
              label=""
              value=""
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-input
            style="width:180px"
            v-model="form.customerBumen"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-input
            style="width:180px"
            v-model="form.customerZhiwei"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-input
            style="width:180px"
            v-model="form.customerAge"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.customerBrith"
            style="width: 180px;"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-input
            style="width:180px"
            v-model="form.customerPhone"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
          <el-input
            style="width:180px"
            v-model="form.customerZphone"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        
        >
           <el-select
            v-model="form.customerJibie"
            placeholder=""
            style="width:180px"
          >
            <el-option
              label=""
              value=""
            ></el-option>
            <el-option
              label=""
              value=""
            ></el-option>
            <el-option
              label=""
              value=""
            ></el-option>
            <el-option
              label=""
              value=""
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item
          label=""
          label-width="80px"
        >
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.customerXiadan"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            style="width: 180px;"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible = false"
        > </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      options1: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      options2: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      value: "",//
      value1: "",
      value2: "",
      tableData: [],
      customerInfo: [],
      dialogFormVisible: false,
      // 
      form: {
        customerName: "",
        customerSex: "",
        customerBumen: "",
        customerZhiwei: "",
        customerAge: "",
        customerBrith: "",
        customerPhone: "",
        customerZphone: "",
        customerJibie: "",
        customerXiadan: ""
      },
      formLabelWidth: "50px"
    }
  },
  methods: {
    deleteRow() {
      this.axios.get("/api/customerInfo.json").then(res => {
        this.customerInfo = res.data.data;
      });
    },
    update(a) {
      this.dialogFormVisible=true;
      this.form = a;
    }
  },
  created() {
    this.axios.get("/api/tableData.json").then(res => {
      this.tableData = res.data.data;
    });
  },
  
};
</script>
<style scoped lang="less">
.box_head {
  margin-top: 10px;
  label {
    margin-right: 5px;
  }
}
.box_content {
  margin-top: 20px;
  table {
    text-align: center;
  }
}
.el-dialog{
  width: 700px;
}
</style>
