<template>
    <div class="box">
        <div class="head_box">
            <label></label>
            <el-input v-model="seach" style="width:217px"></el-input>
            <el-button type="primary" style="margin-left:40px;"></el-button>
        </div>
        <div class="content_box">
            <el-table :data="tableData">
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-table :data="scope.row.product_stock" v-if="scope.row.product_stock != null">
                            <el-table-column label="" prop="supplier_name"></el-table-column>
                            <el-table-column label="" prop="number"></el-table-column>
                            <el-table-column label="" prop="purchase_price"></el-table-column>
                            <el-table-column label="" prop="type">
                                 <template slot-scope="scopes">
                                    <span v-if="scopes.row.type == 0"></span>
                                    <span v-if="scopes.row.type == 1"></span>
                                 </template>
                            </el-table-column> 
                            <el-table-column label="">
                                <template slot-scope="scopes">
                                    <span v-if="scopes.row.status == 0"></span>
                                    <span v-if="scopes.row.status == 1"></span>
                                    <span v-if="scopes.row.status == 2"></span>
                                </template>
                            </el-table-column> 
                            <el-table-column label="">
                                <template slot-scope="scope">
                                    <el-button type="primary" v-if="scope.row.status == 0" size="mini" @click="updata(scope.row)"></el-button>
                                    <el-button type="danger" v-if="scope.row.status == 0" size="mini" @click="deteles(scope.row)"></el-button>
                                    <span v-if="scope.row.status > 0"></span>
                                </template>
                            </el-table-column>     
                        </el-table>
                        <h4 v-else></h4>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span>{{ scope.row.name}}{{ scope.row.class_name}}</span>
                    </template>
                </el-table-column>
                 <el-table-column label="" prop="number"></el-table-column>
                  <el-table-column label="">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 0"></span>
                        <span v-if="scope.row.status == 3"></span>
                        <span v-if="scope.row.status == 5"></span>
                        <span v-if="scope.row.status == 6"></span>
                        <span v-if="scope.row.status == 9"></span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="user_name"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button v-if="scope.row.status < 5" type="primary" size="small" @click="purchase(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="block" style="margin-top: 10px;">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="per_page"
                layout="total, prev, pager, next"
                :total="total">
            </el-pagination>
        </div>


        <el-dialog title="" :visible.sync="dialogPurchase" width="50%">
            <el-form :model="dialogPurchaseForm">
                <el-form-item label="" label-width="90px">
                    <el-select v-model="dialogPurchaseForm.supplier_id" placeholder=""  style="width:300px">
                        <el-option
                        v-for="item in supplier"
                        :key="item.supplier_id"
                        :label="item.supplier_name"
                        :value="item.supplier_id">
                            <span style="float: left">{{ item.supplier_name }}</span>
                            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.number }}---{{ item.selling_price }}</span>
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="90px">
                    <el-radio v-model="dialogPurchaseForm.type" label="0"></el-radio>
                    <el-radio v-model="dialogPurchaseForm.type" label="1"></el-radio>
                </el-form-item>
                <el-form-item label="" label-width="90px">
                    <el-radio v-model="dialogPurchaseForm.settlement " label="0"></el-radio>
                    <el-radio v-model="dialogPurchaseForm.settlement " label="1"></el-radio>
                </el-form-item>
                <el-form-item label="" label-width="90px">
                    <el-input style="width:300px" v-model="dialogPurchaseForm.number"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="90px">
                    <el-input style="width:300px" v-model="dialogPurchaseForm.purchase_price"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogPurchase = false"> </el-button>
                <el-button type="primary" @click="addPurchase"> </el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
export default {
    data(){
        return{
            seach:'',//
            tableData:[], // table 
            currentPage:'',//
            total:'',//
            per_page:'',//
            last_page:'',//
            supplier:[], // 
            dialogPurchase:false,//
            dialogPurchaseForm:{//
              product_stock_id:'',  //  id
              product_sku_id:'',  // id
              number:'',// 
              supplier_id:'', // id
              purchase_price:'',  // 
              type:'', // 
              settlement:'', // 
              id:'',
            }
        }
    },
    methods:{
        // 
        gettableData(){
            this.axios.get('/erp.Purchase/purchase_product_stockup_supplier_lists').then(res => {
                this.tableData = res.data.product_stock.data;
                this.currentPage = res.data.product_stock.current_page;
                this.total = res.data.product_stock.total;
                this.per_page = res.data.product_stock.per_page;
                this.last_page = res.data.product_stock.last_page;
            })
        },
        // fenye
        handleCurrentChange(val) {
            const loading = this.$loading({
                lock: true,
                text: '',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            this.axios.post('/erp.Purchase/purchase_product_stockup_supplier_lists',{
                page:val,
            }).then(res => {
                this.tableData = res.data.product_stock.data;
                this.currentPage = res.data.product_stock.current_page;
                loading.close();
            
            })  
        },
        //   
        getsupplier(a){
            this.axios.get('/erp.Purchase/purchase_product_stockup_supplier_add?product_sku_id='+a).then(res => {
                this.supplier = res.data.product_supplier;
            })
        },
        // 
        purchase(a){
              this.getsupplier(a.product_sku_id);
              this.dialogPurchase = true;
              this.dialogPurchaseForm.product_stock_id = a.id;  
              this.dialogPurchaseForm.product_sku_id = a.product_sku_id;  
              this.dialogPurchaseForm.number = ''; 
              this.dialogPurchaseForm.supplier_id = ''; 
              this.dialogPurchaseForm.purchase_price = ''; 
              this.dialogPurchaseForm.id = '';
              this.dialogPurchaseForm.type = '';
              this.dialogPurchaseForm.settlement = '0';
        },
        // 
        updata(a){
              this.getsupplier(a.product_sku_id);
              this.dialogPurchase = true;
              this.dialogPurchaseForm.product_stock_id = a.product_stock_id;  
              this.dialogPurchaseForm.product_sku_id = a.product_sku_id;  
              this.dialogPurchaseForm.number = a.number; 
              this.dialogPurchaseForm.supplier_id = a.supplier_id; 
              this.dialogPurchaseForm.purchase_price = a.purchase_price; 
              this.dialogPurchaseForm.id = a.id;
              this.dialogPurchaseForm.type = String(a.type);
              this.dialogPurchaseForm.settlement = String(a.settlement);
        },
        //      
        addPurchase(){
            if(this.dialogPurchaseForm.id == ''){
                this.axios.post('/erp.Purchase/purchase_product_stockup_supplier_add',{
                    product_stock_id:this.dialogPurchaseForm.product_stock_id,
                    product_sku_id:this.dialogPurchaseForm.product_sku_id,
                    number:this.dialogPurchaseForm.number,
                    supplier_id:this.dialogPurchaseForm.supplier_id,
                    purchase_price:this.dialogPurchaseForm.purchase_price,
                    money:`${this.dialogPurchaseForm.number * this.dialogPurchaseForm.purchase_price}`,
                    type:this.dialogPurchaseForm.type,
                    settlement:this.dialogPurchaseForm.settlement,
                }).then(res => {
                    if(res.data.code == 2000){
                        this.open(res.data.msg,'success');
                        this.gettableData();
                        this.dialogPurchase = false;
                    }else{
                        this.open(res.data.msg,'error');
                    } 
                })
            }else{
               this.axios.post('/erp.Purchase/purchase_product_stockup_supplier_update',{
                    product_stock_id:this.dialogPurchaseForm.product_stock_id,
                    product_sku_id:this.dialogPurchaseForm.product_sku_id,
                    number:this.dialogPurchaseForm.number,
                    supplier_id:this.dialogPurchaseForm.supplier_id,
                    purchase_price:this.dialogPurchaseForm.purchase_price,
                    money:`${this.dialogPurchaseForm.number * this.dialogPurchaseForm.purchase_price}`,
                    type:this.dialogPurchaseForm.type,
                    settlement:this.dialogPurchaseForm.settlement,
                    id:this.dialogPurchaseForm.id,
                }).then(res => {
                    if(res.data.code == 2000){
                        this.open(res.data.msg,'success');
                        this.gettableData();
                        this.dialogPurchase = false;
                    }else{
                        this.open(res.data.msg,'error');
                    } 
                }) 
            }
        },
        
        //
        deteles(a){
           this.axios.post('/erp.Purchase/purchase_product_stockup_supplier_delete',{
                id:a.id
            }).then(res => {
                if(res.data.code == 2000){
                    this.open(res.data.msg,'success');
                    this.gettableData();
                }else{
                    this.open(res.data.msg,'error');
                } 
            }) 
        },
         open(a,b){
            this.$message({
                message: a,
                type: b
            });
        },    
    },
    created(){
        this.gettableData();
    },
    watch:{
        $route(to){
        this.gettableData();
        }
    }
}
</script>

<style lang="less" scoped>
.box{
    .head_box{
        margin-top: 20px;
    }
}
</style>
